<template>
  <div>
    <el-container>
      <el-main>
        <div v-for="item in list" @click="turnUser(item.id)">
          <el-row :gutter="20">
            <el-col :span="2"
              ><div class="grid-content bg-purple"></div
            ></el-col>
            <el-col :span="18"
              ><div class="grid-content bg-purple">
                {{ item.cname }}
              </div></el-col
            >
            <el-col :span="4"
              ><div class="grid-content bg-purple"></div
            ></el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col :span="2"
              ><div class="grid-content bg-purple"></div
            ></el-col>
            <el-col :span="18"
              ><div class="grid-content bg-purple">
                {{ item.address }}
              </div></el-col
            >
            <el-col :span="4"
              ><div class="grid-content bg-purple"></div
            ></el-col>
          </el-row>
          <el-row :gutter="20" style="margin-bottom:-20px;">
            <el-col :span="2"
              ><div class="grid-content bg-purple"></div
            ></el-col>
            <el-col :span="18"
              ><div class="grid-content bg-purple">
                <i class="el-icon-phone-outline"></i
                >{{ item.linkmanList[0].ltel }}
              </div></el-col
            >
            <el-col :span="4"
              ><div class="grid-content bg-purple"></div
            ></el-col>
          </el-row>
          <el-divider></el-divider>
        </div>
      </el-main>
    </el-container>
  </div>
</template>

<script>
export default {
  name: "",
  props: ["blurList"],
  data() {
    return {
      list: "",
      aList: []
    };
  },
  watch: {
    blurList: function(newVal, oldVal) {
      this.aList = newVal; //newVal即是chartData
    },
    aList: function(newVal, oldVal) {
      this.list = newVal;
    }
  },
  mounted() {
    this.getList();
  },

  methods: {
    getList() {
      this.axios
        .get(this.baseUrl + "client/queryAllClients")
        .then(res => {
          this.list = res.data;
        })
        .catch(err => {});
    }
  }
};
</script>

<style scoped>
.el-tag {
  position: absolute;
  right: 60px;
}
.el-row {
  margin-bottom: 10px;
}
.el-col {
  border-radius: 4px;
}

.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
.row-bg {
  padding: 10px 0;
}
</style>



